<template>
  <div>
    <div class="banner-text">
      <div class="left-con">
        <div class="headimg">
          <img :src="userInfo.img" />
        </div>
        <span>{{ userInfo.name }}</span>
      </div>
      <van-icon
        @click="show = !show"
        name="wap-nav"
        color="#ffffff"
        size="24px"
      />
    </div>
    <van-cell is-link @click="showUserInfo = !showUserInfo">
      <template slot="icon">
        <span class="iconfont icon-shenqingfapiao"></span>
      </template>
      <template slot="title">
        <span class="custom-text" style="margin-left:10px">
          身份信息
        </span>
      </template>
    </van-cell>
    <van-cell is-link to="address">
      <template slot="icon">
        <span class="iconfont icon-geren9"></span>
      </template>
      <template slot="title">
        <span class="custom-text" style="margin-left:10px">
          地址信息
        </span>
      </template>
    </van-cell>
    <van-cell is-link to="historyPage">
      <template slot="icon">
        <span class="iconfont icon-geren9"></span>
      </template>
      <template slot="title">
        <span class="custom-text" style="margin-left:10px">
          申请记录
        </span>
      </template>
    </van-cell>
    <van-cell @click="clearUserInfo">
      <template slot="icon">
        <span class="iconfont icon-xunwen"></span>
      </template>
      <template slot="title">
        <span class="custom-text" style="margin-left:10px">
          退出登录
        </span>
      </template>
    </van-cell>
    <van-popup v-model="show" position="right" style="width:30%;height:100%">
      <van-cell-group>
        <van-cell title="首页" is-link to="home" />
        <van-cell title="个人中心" is-link to="person" />
      </van-cell-group>
    </van-popup>
    <van-popup
      v-model="showUserInfo"
      position="right"
      style="width:100%;height:100%"
    >
      <van-nav-bar
        title="身份信息"
        left-text="返回"
        left-arrow
        @click-left="offList"
      />
      <van-cell-group>
        <van-cell title="姓名" :value="name" />
        <van-cell title="实名认证状态" :value="stats" />
        <van-cell title="手机号" :value="phone" />
      </van-cell-group>
    </van-popup>
  </div>
</template>
<script>
import { getUserInfo, delUserInfo } from "@/utils/userInfo";
import { loginOpenid } from "@/api/user";
export default {
  name: "Person",
  data() {
    return {
      show: false,
      userInfo: {
        img: "",
        name: ""
      },
      showUserInfo: false,
      name: "",
      stats: "",
      phone: ""
    };
  },
  mounted: function() {
    let Info = getUserInfo();
    this.userInfo.img = Info.headimgurl;
    this.userInfo.name = Info.nickname;
    loginOpenid(Info.openid).then(res => {
      if (res.data) {
        this.name = res.data.NAME;
        if (res.data.SMRZZT === "PASS") {
          this.stats = "已认证";
        } else {
          this.stats = "未认证";
        }
        this.phone = res.data.PHONENO;
      }
    });
  },
  methods: {
    clearUserInfo() {
      delUserInfo();
      this.$router.push({ path: "/login" });
    },
    offList() {
      this.showUserInfo = false;
    }
  }
};
</script>
<style lang="stylus" scoped>
.person-con {
  width 100%
  padding 30px 10px
  height 2rem
  display flex
  background: #409eff
  .person-head {
    width 50px
    height 50px
    border-radius 25px
    overflow hidden
    img {
      width 100%
      height 100%
    }
  }
  .mingcheng {
    margin-left .4rem
    margin-top .4rem
    color #ffffff
  }
}
.banner-text
  width 100%
  height 1.8rem
  display flex
  padding 10px 10px
  align-items center
  justify-content space-between
  border-bottom  1px solid #f2f2f2
  background #409eff
  .left-con
    display flex
    color #ffffff
    align-items center
    span
      margin-left 20px
    .headimg
      width 50px
      height 50px
      border-radius 25px
      overflow hidden
      img
        width 100%
        height 100%
</style>
