<template>
  <div class="connect">
    <bannerTop :headtitle="NavText"></bannerTop>
    <van-cell-group @click="show = !show">
      <van-field v-model="value" placeholder="选择申报企业" disabled />
    </van-cell-group>
    <van-popup v-model="show" position="bottom">
      <van-picker :columns="columns" @change="onChange" />
    </van-popup>
    <div class="login-info">
      <van-button
        size="large"
        type="info"
        @click="goShenBao"
        style="height: 38px;line-height:38px"
      >
        提交
      </van-button>
    </div>
  </div>
</template>
<script>
import bannerTop from "@/components/publichead";
import { queryQyxxlist } from "@/api/user";
export default {
  name: "Zero",
  components: {
    bannerTop
  },
  data() {
    return {
      paragraph: "",
      NavText: "零申报",
      value: "",
      show: false,
      columns: [],
      QYlist: []
    };
  },
  created: function() {
    let list = [];
    queryQyxxlist("45").then(res => {
      if (res.data) {
        this.stats = false;
        for (let i = 0; i < res.data.length; i++) {
          let name = {
            text: res.data[i].QYMC,
            QYSH: res.data[i].QYSH,
            QYID: res.data[i].QYID
          };
          list.push(name);
          this.columns = list;
        }
      } else {
        this.stats = true;
      }
    });
  },
  methods: {
    // eslint-disable-next-line
    onChange(picker, value, index) {
      this.value = "当前选择公司：" + value.text;
    },
    goShenBao() {
      this.$dialog.alert.confirm({ title: "是否进行零申报" }).then(() => {});
    }
  }
};
</script>
<style lang="stylus" scoped>
.connect
  width 100%
  height 100%
  padding-top 1.5rem
  background #ffffff
  .connect-user
    width 100%
    height 1.5rem
    text-align center
    line-height 1.5rem
    background #409eff
    font-size 16px
    font-weight 520
    color #ffffff
  .login-info
    width 100%
    height 100%
    text-align right
    padding .1rem .2rem
    background #ffffff
</style>
