<template>
  <div class="connect">
    <bannerTop :headtitle="NavText"></bannerTop>
    <van-cell-group>
      <van-cell title="已绑定企业" />
      <van-cell
        v-for="(item, idx) in QYlist"
        :key="idx"
        :title="item.QYMC"
        :value="item.QYSH"
      />
    </van-cell-group>
    <van-cell-group style="height:100%" v-if="hid">
      <van-cell title="添加绑定信息" />
      <van-field
        v-model="usernumber"
        required
        label="身份证号"
        @blur="checkUserNumber"
        :error-message="numError"
        right-icon="user-circle-o"
        placeholder="请输入身份证号"
      />
      <van-field
        v-model="paragraph"
        required
        label="企业税号"
        @blur="checkQYSH"
        :error-message="QYSHError"
        right-icon="user-circle-o"
        placeholder="请输入企业税号"
      />
    </van-cell-group>
    <div class="login-info">
      <van-button
        @click="addCpNum"
        size="large"
        type="info"
        style="height: 38px;line-height:38px"
      >
        添加绑定
      </van-button>
    </div>
  </div>
</template>
<script>
import bannerTop from "@/components/publichead";
import { queryQyxxlist, bandQyxx } from "@/api/user";
import { validatUserNumber, QYNumber } from "@/utils/validate";
import { getUserInfo } from "@/utils/userInfo";
export default {
  name: "Connect",
  components: {
    bannerTop
  },
  data() {
    return {
      paragraph: "",
      usernumber: "",
      NavText: "企业绑定",
      QYlist: [],
      numError: "",
      QYSHError: "",
      hid: false
    };
  },
  created: function() {
    // 获取企业绑定列表
    let id = getUserInfo().YHID;
    queryQyxxlist(id).then(res => {
      if (!res.data) {
        this.$toast("暂无绑定信息");
      } else {
        this.QYlist = res.data;
        console.log(this.QYlist);
      }
    });
  },
  methods: {
    // 验证身份证号
    checkUserNumber() {
      if (!validatUserNumber(this.usernumber)) {
        this.numError = "请输入正确身份证号";
      } else {
        this.numError = "";
      }
    },
    // 验证企业税号
    checkQYSH() {
      if (!QYNumber(this.paragraph)) {
        this.QYSHError = "请输入正确企业税号";
      } else {
        this.QYSHError = "";
      }
    },
    // 添加绑定信息
    addCpNum() {
      this.hid = true;
      let id = getUserInfo().YHID;
      if (validatUserNumber(this.usernumber) || QYNumber(this.paragraph)) {
        bandQyxx(id, this.usernumber, this.paragraph).then(res => {
          if (res.data.STATUS === "SUCCESS") {
            this.$router.push({ path: "/home" });
          }
        });
      }
    }
  }
};
</script>
<style lang="stylus" scoped>
.connect
  width 100%
  height 100%
  padding-top 1.5rem
  background #ffffff
  .connect-user
    width 100%
    height 1.5rem
    text-align center
    line-height 1.5rem
    background #409eff
    font-size 16px
    font-weight 520
    color #ffffff
  .login-info
    width 100%
    height 100%
    text-align right
    padding .1rem .2rem
    background #ffffff
</style>
