<template>
  <div class="renzheng">
    <bannerTop :headtitle="NavText"></bannerTop>
    <van-cell-group v-if="showRZ">
      <van-cell title="姓名" :value="name" />
      <van-cell title="身份证号" :value="userSFZH" />
    </van-cell-group>
    <div class="uploadImg" v-if="!showRZ">
      <form action="" id="RenImg">
        <van-cell-group>
          <van-field
            type="text"
            label="姓名"
            placeholder="请输入姓名"
            v-model="username"
          />
          <van-field
            type="text"
            label="身份证号"
            v-model="SFZH"
            @blur="checkNumber"
            :error-message="checksf"
            placeholder="请输入身份证号"
          />
          <van-cell title="采集照片">
            <!-- <input
              type="file"
              name="CJZP"
              accept="image/*"
              capture="camera"
              @change="checkfile"
            /> -->
            <van-uploader :after-read="onReadCai" name="SFZHFM" ref="CJZP">
              <van-icon name="photograph" />
            </van-uploader>
          </van-cell>
          <van-cell title="身份证正面照片">
            <!-- <input
              type="file"
              name="SFZHZM"
              accept="image/*"
              capture="camera"
              @change="checkfile"
            /> -->
            <van-uploader :after-read="onReadZM" name="SFZHZM" ref="SFZHZM">
              <van-icon name="photograph" />
            </van-uploader>
          </van-cell>
          <van-cell title="身份证反面照片">
            <!-- <input
              type="file"
              name="SFZHFM"
              accept="image/*"
              capture="camera"
              @change="checkfile"
            /> -->
            <van-uploader :after-read="onReadFM" name="SFZHFM">
              <van-icon name="photograph" />
            </van-uploader>
          </van-cell>
        </van-cell-group>
      </form>
      <div class="login-info">
        <van-button
          @click="onSubmit"
          size="large"
          type="info"
          v-if="!showloading"
          style="height: 38px;line-height:38px"
        >
          提交
        </van-button>
        <div class="loading" v-if="showloading">
          <van-loading type="spinner" color="#c9c9c9" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import alldata from "@/utils/navdata";
import bannerTop from "@/components/publichead";
import axios from "axios";
import { HttpUrl } from "@/utils/http";
import { validatUserNumber } from "@/utils/validate";
import { getUserInfo } from "@/utils/userInfo";
import { loginOpenid } from "@/api/user";
import lrz from "lrz";
export default {
  name: "Serve",
  components: {
    bannerTop
  },
  data() {
    return {
      title: "纳税服务",
      alldata: alldata,
      NavText: "实名认证",
      progress: 0,
      username: "",
      activeName: "1",
      checksf: "",
      SFZH: "",
      YHID: "",
      OpenId: "",
      name: "",
      userSFZH: "",
      showRZ: false,
      showloading: false,
      ysImgs: [],
      imgsBase64: {
        CJZP: "",
        SFZHZM: "",
        SFZHFM: "",
        OPENID: "",
        YHID: ""
      }
    };
  },
  mounted: function() {
    let userInfo = getUserInfo();
    this.imgsBase64.YHID = "121";
    this.imgsBase64.OPENID = "oKW8c6K12x4c4kDylK6w_uxce9mE";
    loginOpenid(userInfo.openid).then(res => {
      if (res.data.SMRZZT === "PASS") {
        this.showRZ = true;
        this.name = res.data.NAME;
        this.userSFZH = res.data.SFZH;
      }
    });
  },
  methods: {
    async onSubmit() {
      const that = this;
      that.showloading = true;
      let config = {
        processData: false,
        traditional: true,
        contentType: false
      };
      var fd = that.imgsBase64;
      await axios
        .post(HttpUrl + "/smrz/save.action", fd, config)
        .then(function(response) {
          if (response.data) {
            let msg = "失败错误码";
            if (msg.indexOf(response.data.msg) > 0) {
              that.$toast("服务错误");
            }
            // 关闭加载
            that.showloading = false;
            // 跳转页面
            that.$router.push("/home");
          } else {
            that.showloading = false;
            let msg = "失败错误码";
            if (msg.indexOf(response.data.msg) > 0) {
              that.$toast("服务错误");
            }
          }
        });
    },
    // 检查身份证号
    checkNumber() {
      if (validatUserNumber(this.SFZH)) {
        this.checksf = "";
      } else {
        this.checksf = "身份证号不正确";
      }
    },
    onReadCai(val) {
      this.imgsBase64.CJZP = val.content;
    },
    onReadZM(val) {
      this.imgsBase64.SFZHZM = val.content;
    },
    onReadFM(val) {
      this.imgsBase64.SFZHFM = val.content;
    },
    /* eslint-disable */
    checkfile(e) {
      var isIE = /msie/i.test(navigator.userAgent) && !window.opera;
      var fileSize = 0;
      var filetypes = [".jpg", ".png", ".jpeg", ".gif", ".svg"];
      var filepath = e.target.value;
      // 压缩前
      console.log(e.srcElement.files[0]);
      lrz(e.srcElement.files[0])
        .then(function (rst) {
          // 压缩后
          this.ysImgs.push(rst.formData);
          console.log(this.ysImgs);
          console.log(rst);
        })
      var filemaxsize = 1024 * 10;//2M
      if (filepath) {
        var isnext = false;
        var fileend = filepath.substring(filepath.lastIndexOf("."));
        if (filetypes && filetypes.length > 0) {
          for (var i = 0; i < filetypes.length; i++) {
            if (filetypes[i] == fileend) {
              isnext = true;
              break;
            }
          }
        }
        if (!isnext) {
          this.$toast("不接受此文件类型！");
          e.target.value = "";
          return false;
        }
      } else {
        return false;
      }
      if (isIE && !target.files) {
        var filePath = e.target.value;
        var fileSystem = new ActiveXObject("Scripting.FileSystemObject");
        if (!fileSystem.FileExists(filePath)) {
          this.$toast("附件不存在，请重新输入！");
          return false;
        }
        var file = fileSystem.GetFile(filePath);
        fileSize = file.Size;
      } else {
        fileSize = e.target.files[0].size;
      }
      var size = fileSize / 1024;
      if (size > filemaxsize) {
        this.$toast("附件大小不能大于" + filemaxsize / 1024 + "M！");
        e.target.value = "";
        return false;
      }
      if (size <= 0) {
        this.$toast("附件大小不能为0M！");
        e.target.value = "";
        return false;
      }
    }
  }
};
</script>
<style lang="stylus">
.renzheng {
  width: 100%;
  height: auto;
  padding-top: 1.5rem;
}

.a-upload:hover {
  color: #FFFFFF;
  background: #337ab7;
  border-color: #204d74;
  text-decoration: none;
}
.uploadImg {
  width: 100%;
  height: 8rem;
  background: #ffffff;
}
.progress {
  width 100%
  height 100%
  position absolute
  z-index 300
  background rgba(0,0,0,.5)
  margin auto
  top 0
  left 0
  right 0
  bottom 0
  display flex
  justify-content center
  align-items center
}
.login-info {
  width: 100%;
  margin-top .2rem
  text-align: right;
  padding: 0.1rem 0.2rem;
  background: #ffffff;
  .loading {
    width 100%
    height 100px
    padding 0 20px
    display flex
    justify-content center
  }
}

.imgs {
  width: 100%;
  height: 3rem;
  padding: 0 0.4rem;
  img {
    width: 100%;
    height: 100%;
  }
}
</style>
