<template>
  <div class="signin">
    <div class="signin-user">
      <span>用户信息注册</span>
    </div>
    <van-cell-group style="height:100%">
      <van-field
        v-model="phone"
        required
        label="手机号"
        right-icon="user-circle-o"
        placeholder="请输入手机号"
      />
      <van-field
        v-model="password"
        type="password"
        label="密码"
        placeholder="请输入密码"
        right-icon="weapp-nav"
        required
      />
      <van-field
        v-model="password1"
        type="password"
        label="密码"
        placeholder="确认密码"
        right-icon="weapp-nav"
        required
      />
      <van-field
        v-model="sms"
        center
        required
        :value="jym"
        label="短信验证码"
        placeholder="请输入短信验证码"
      >
        <van-button @click="getInfo" slot="button" size="small" type="primary">
          发送验证码
        </van-button>
      </van-field>
      <van-field
        v-model="ywm"
        type="hidden"
        label="业务码"
        placeholder="请输入业务码"
        right-icon="weapp-nav"
        required
      />
    </van-cell-group>
    <div class="signin-info">
      <span @click="gotologin">已注册？</span>
      <van-button size="large" @click="addUserInfo" type="info">
        注册信息
      </van-button>
    </div>
  </div>
</template>
<script>
import { getYzmYwm, addUser } from "@/api/user";
import { getUserInfo } from "@/utils/userInfo";
export default {
  name: "Serve",
  components: {},
  data() {
    return {
      phone: "",
      password: "",
      sms: "",
      password1: "",
      ywm: "",
      jym: "",
      timer: null
    };
  },
  methods: {
    getInfo() {
      // 获取验证码
      getYzmYwm(this.phone).then(res => {
        this.ywm = res.data.YWM;
        this.jym = res.data.JYM;
      });
    },
    addUserInfo() {
      let openid = getUserInfo().openid;
      // eslint-disable-next-line
      addUser(this.sms, openid, this.password, this.password1, this.phone, this.ywm).then(res => {
        if (res.data.YHID != "") {
          this.$router.push({ path: "/home" });
        }
      });
    },
    gotologin() {
      this.$router.push({ path: "/login" });
    }
  }
};
</script>
<style lang="stylus" scoped>
.signin
  width 100%
  height 100%
  background #ffffff
  .signin-user
    width 100%
    height 1.5rem
    text-align center
    line-height 1.5rem
    background #ffffff
    font-size 16px
    font-weight 520
  .signin-info
    width 100%
    height 100%
    text-align right
    padding .1rem .2rem
    background #ffffff
    span
      line-height 1rem
</style>
