<template>
  <div class="connect">
    <bannerTop :headtitle="NavText"></bannerTop>
    <van-cell-group v-if="type === '1'" @click="show = !show">
      <van-field v-model="value" placeholder="选择绑定企业" disabled />
    </van-cell-group>
    <van-popup v-model="show" position="bottom">
      <van-picker :columns="columns" @change="onChange" />
    </van-popup>
    <!--开始结束时间-->
    <van-cell title="开始时间" :value="KaiTime" @click="funstartTime" />
    <van-cell title="结束时间" :value="JieTime" @click="funendTime" />
    <van-popup v-model="showStartTime" position="bottom">
      <van-datetime-picker
        v-model="startTime"
        @confirm="makeStart"
        type="date"
        :min-date="minDate"
      />
    </van-popup>
    <van-popup v-model="showEndTime" position="bottom">
      <van-datetime-picker
        v-model="endTime"
        @confirm="makeEnd"
        type="date"
        :min-date="minDate"
      />
    </van-popup>
    <!--搜索明细-->
    <div class="search-con">
      <div class="search-btn">
        <van-button size="small" type="info">查询明细</van-button>
      </div>
      <van-cell-group>
        <div class="mxcon">
          <div class="mxcon-item">
            <span>税种：<span>11111</span></span>
            <span>实缴金额：<span>2222222</span></span>
            <span>交款日期：<span>3333</span></span>
          </div>
          <div class="mxcon-item">
            <span>税种：<span>111111</span></span>
            <span>实缴金额： <span>2222222</span></span>
            <span>交款日期：<span>3333</span></span>
          </div>
        </div>
        <div class="search-btn">
          <router-link to="/pdf">
            <van-button size="small" type="info">预览完税证明</van-button>
          </router-link>
        </div>
        <van-field
          v-model="Email"
          center
          clearable
          label="邮箱"
          placeholder="请输入正确邮箱地址"
        >
          <van-button slot="button" size="small" type="primary">
            发送至邮箱
          </van-button>
        </van-field>
      </van-cell-group>
    </div>
  </div>
</template>
<script>
import bannerTop from "@/components/publichead";
import { formatOne, dateFormat } from "@/utils/time.js";
export default {
  name: "WanShui",
  components: {
    bannerTop
  },
  data() {
    return {
      NavText: "电子完税",
      value: "",
      show: false,
      showTime: false,
      showStartTime: false,
      showEndTime: false,
      startTime: new Date(),
      endTime: new Date(),
      minDate: new Date(),
      Email: "",
      type: 1,
      KaiTime: dateFormat(new Date().getTime(), "yyyy-MM-dd"),
      JieTime: dateFormat(new Date().getTime(), "yyyy-MM-dd"),
      columns: ["测试公司一", "测试公司二", "测试公司三"]
    };
  },
  created: function() {
    let type = this.$route.query.type;
    this.type = type;
  },
  methods: {
    onChange(picker, value, index) {
      this.value = "当前选择公司：" + value;
      this.$toast(`当前值：${value}, 当前索引：${index}`);
    },
    // 显示隐藏开始时间
    funstartTime() {
      this.showStartTime = !this.showStartTime;
    },
    // 显示隐藏结束时间
    funendTime() {
      this.showEndTime = !this.showEndTime;
    },
    //开始时间
    makeStart(val) {
      let start = val;
      let now = formatOne(start, "yyyy-MM-dd");
      this.KaiTime = now;
      let timeNum = new Date(val).getTime() / 1000;
      console.log(now + timeNum);
      this.funstartTime();
    },
    // 结束时间
    makeEnd(val) {
      let end = val;
      let now = formatOne(end, "yyyy-MM-dd");
      this.JieTime = now;
      let timeNum = new Date(val).getTime() / 1000;
      console.log(now + timeNum);
      this.funendTime();
    }
  }
};
</script>
<style lang="stylus" scoped>
.search-con {
  width 100%
  height auto
  background #ffffff
  .search-btn {
    width 100%
    height 50px
    line-height 50px
    text-align right
    padding 0 10px;
  }
  .mxcon {
    width 100%
    height auto
    padding 0 .4rem
    .mxcon-item {
      width 100%
      height 0.8rem
      display flex
      justify-content space-between
      border-bottom 1px solid #dfdfdf
      line-height .8rem
    }
  }
}
</style>
