<template>
  <div class="connect">
    <bannerTop :headtitle="NavText"></bannerTop>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      style="margin-top:1.5rem"
      @load="load_more_items"
    >
      <div class="listCon" v-for="(item, idx) in findFPFSList" :key="idx">
        <p>
          <span>
            企业名称：<span>{{ item.QYMC }}</span>
          </span>
          <span>
            企业税号：<span>{{ item.QYSH }}</span>
          </span>
        </p>
        <p>
          <span>
            总金额：<span>{{ item.ZJE }}</span>
          </span>
          <span>
            总税额：<span>{{ item.ZSE }}</span>
          </span>
        </p>
        <p>
          <span>
            购方税号：<span>{{ item.GFSH }}</span>
          </span>
          <span>
            邮寄单号：<span>{{ item.YJORDERNO }}</span>
          </span>
        </p>
      </div>
    </van-list>
  </div>
</template>
<script>
import bannerTop from "@/components/publichead";
import { findZPDK } from "@/api/user";
import { getUserInfo } from "@/utils/userInfo";
export default {
  name: "FSSQ",
  components: {
    bannerTop
  },
  data() {
    return {
      NavText: "发售代开记录",
      findFPFSList: [],
      findZPDKList: [],
      showFPDS: false,
      list: [],
      loading: false,
      finished: false,
      page: 0,
      offset: 0,
      limit: 6
    };
  },
  methods: {
    load_more_items: function() {
      this.page += 1;
      this.offset = this.limit * this.page;
      this.onLoad();
    },
    onLoad() {
      // 异步更新数据
      const that = this;
      let Info = getUserInfo();
      findZPDK(Info.YHID, 6, this.page).then(res => {
        that.loading = false;
        let data = res.data.MXDETAIL;
        if (this.findFPFSList.length != Number(res.data.TOTALROW)) {
          for (let i = 0; i < data.length; i++) {
            that.findFPFSList.push(data[i]);
          }
        } else {
          that.loading = false;
          that.finished = true;
        }
        console.log(that.findFPFSList);
      });
    }
  }
};
</script>
<style lang="css" scoped>
.connect {
  width: 100%;
  height: auto;
  padding-top: 1.5rem;
}
.listCon {
  width: 100%;
  height: auto;
  line-height: .6rem;
  padding: 0.1rem .4rem;
  border-bottom: 1px solid #dfdfdf;
  background: #ffffff;
}
.listCon p {
  display: flex;
  justify-content: space-between;
  text-align: left;
}
</style>
