<template>
  <div class="login" :style="'height:' + height + 'px'">
    <div class="login-user">
      <span>用户登录</span>
    </div>
    <van-cell-group v-if="!type">
      <van-field
        v-model="phone"
        required
        clearable
        label="手机号"
        right-icon="user-circle-o"
        placeholder="请输入手机号"
      />
      <van-field
        v-model="password"
        type="password"
        label="密码"
        placeholder="请输入密码"
        right-icon="weapp-nav"
        required
      />
    </van-cell-group>
    <van-cell-group v-if="type">
      <van-field
        v-model="phone"
        required
        clearable
        label="手机号"
        right-icon="user-circle-o"
        placeholder="请输入手机号"
      />
      <van-field
        v-model="JYM"
        center
        clearable
        label="短信验证码"
        placeholder="请输入短信验证码"
      >
        <van-button @click="getYzm" slot="button" size="small" type="primary">
          发送验证码
        </van-button>
      </van-field>
    </van-cell-group>
    <div class="login-info">
      <span style="float:left;" @click="adduser">注册</span>
      <span @click="type = !type">
        {{ type ? "密码登录" : "验证码登录" }}
      </span>
      <van-button @click="loginUser" size="large" type="info">登录</van-button>
    </div>
  </div>
</template>
<script>
import { loginPwd, loginYzm, getYzmYwm } from "@/api/user";
import { getUserInfo, setUserInfo } from "@/utils/userInfo";
export default {
  name: "Login",
  components: {},
  data() {
    return {
      phone: "",
      password: "",
      yzm: "",
      type: false,
      height: window.screen.height,
      JYM: "",
      YWM: ""
    };
  },
  methods: {
    loginUser() {
      // 判断登录类型进行登录
      let alldata = getUserInfo();
      let openID = alldata.openid;
      if (!this.type) {
        loginPwd(this.phone, this.password, openID).then(result => {
          let results = result.data;
          this.setUser(alldata, results);
          this.$router.push({ path: "/home" });
        });
      } else {
        loginYzm(this.JYM, this.YWM, this.phone, openID).then(res => {
          let resdata = res.data;
          this.setUser(alldata, resdata);
          this.$router.push({ path: "/home" });
        });
      }
    },
    adduser() {
      this.$router.push({ path: "/sigin" });
    },
    // 获取验证码
    getYzm() {
      getYzmYwm(this.phone).then(res => {
        if (res.data) {
          this.YWM = res.data.YWM;
          this.JYM = res.data.JYM;
        }
      });
    },
    // 设置用户信息
    setUser(alldata, userObj) {
      if (userObj) {
        setUserInfo(
          alldata.headimgurl,
          alldata.nickname,
          alldata.openid,
          userObj.YHID,
          userObj.PHONENO,
          userObj.SMRZZT,
          userObj.SFZH,
          userObj.NAME
        );
      }
    }
  }
};
</script>
<style lang="stylus" scoped>
.login
  width 100%
  overflow hidden
  background #ffffff
  .login-user
    width 100%
    height 1.5rem
    text-align center
    line-height 1.5rem
    background #ffffff
    font-size 16px
    font-weight 520
  .login-info
    width 100%
    height 100%
    text-align right
    padding .1rem .2rem
    background #ffffff
    span
      line-height 1rem
</style>
