<template>
  <div class="connect">
    <bannerTop :headtitle="NavText"></bannerTop>
    <van-cell title="选择绑定企业" />
    <van-cell-group @click="show = !show">
      <van-field v-model="value" placeholder="点击选择绑定企业" disabled />
    </van-cell-group>
    <van-popup v-model="show" position="bottom">
      <van-picker :columns="columns" @change="onChange" :loading="stats" />
    </van-popup>
    <van-cell title="选择发票类型" />
    <div class="fpType">
      <div
        class="fpType-item"
        v-for="(item, idx) in sort"
        :key="idx"
        @click="getCheck(idx, item.FPZLDM)"
      >
        <div class="check-btn" v-if="checked === idx">
          <span class="iconfont icon-Checkout "></span>
        </div>
        <p>
          <span>发票种类代码：</span>
          <span>{{ item.FPZLDM }}</span>
        </p>
        <p>
          <span>发票名称</span>
          <span>{{ item.FPMC }}</span>
        </p>
        <p>
          <span>核定数量</span>
          <span>{{ item.HDSL }}</span>
        </p>
        <p>
          <span>结存数量</span>
          <span>{{ item.JCSL }}</span>
        </p>
        <p>
          <span>可够数量</span>
          <span>{{ item.KGSL }}</span>
        </p>
      </div>
    </div>
    <van-field
      v-model="fenshu"
      required
      type="number"
      label="领用份数"
      placeholder="请输入领用份数"
    />
    <van-radio-group v-model="lingType">
      <van-cell-group>
        <van-cell title="邮寄" clickable @click="lingType = '1'">
          <span>{{ userInfo.address }}</span>
          <van-radio name="1" />
        </van-cell>
        <van-cell title="自助机领取" clickable @click="lingType = '2'">
          <van-radio name="2" />
        </van-cell>
      </van-cell-group>
      <div class="login-info">
        <van-button
          size="large"
          type="info"
          style="height: 38px;line-height:38px;"
          @click="pushCon"
        >
          提交
        </van-button>
      </div>
    </van-radio-group>
    <!--地址列表-->
    <van-popup
      v-model="ShowAddress"
      position="right"
      :overlay="false"
      style="width:100%;height:100%;overflow:hidden"
    >
      <van-nav-bar
        title="地址列表"
        left-text="返回"
        left-arrow
        @click-left="offList"
      />
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        @add="onAdd"
        @select="chooseAddress"
      />
    </van-popup>
    <!--新增地址-->
    <van-popup
      v-model="addAddress"
      position="right"
      :overlay="false"
      style="width:100%;height:100%;overflow:hidden;"
    >
      <van-nav-bar
        title="新增地址"
        left-text="返回"
        left-arrow
        @click-left="offAdd"
      />
      <van-address-edit :area-list="areaList" @save="onSave" />
    </van-popup>
  </div>
</template>
<script>
import bannerTop from "@/components/publichead";
import {
  queryQyxxlist,
  getAddress,
  saveAddress,
  faPiaoPush,
  getFptype
} from "@/api/user";
import areaList from "../../../public/web/area/area";
import { getUserInfo } from "@/utils/userInfo";
export default {
  name: "FaShou",
  components: {
    bannerTop
  },
  data() {
    return {
      paragraph: "",
      NavText: "发票发售",
      value: "",
      show: false,
      columns: [],
      stats: false,
      fenshu: "",
      sort: [],
      checked: false,
      lingType: "2",
      ShowAddress: false,
      chosenAddressId: "1",
      addAddress: false,
      FPZLDM: "",
      getLYid: [],
      list: [],
      nowAddress: "",
      userInfo: {
        name: "",
        tel: "",
        address: ""
      },
      areaList: areaList,
      QYInfo: {
        QYSH: "",
        QYID: ""
      }
    };
  },
  watch: {
    lingType: function(val) {
      if (val === "1") {
        this.getaddressList();
      }
    }
  },
  /* eslint-disable */
  created: function() {
    let list = [];
    // 获取企业列表
    let id = getUserInfo().YHID;
    queryQyxxlist(id).then(res => {
      this.value = "当前选择公司：" + res.data[0].QYMC;
      this.QYInfo.QYSH = res.data[0].QYSH;
      this.QYInfo.QYID = res.data[0].QYID;
      this.getFPsort(res.data[0].QYSH);
      if (res.data) {
        this.stats = false;
        for (let i = 0; i < res.data.length; i++ ) {
          let name = {
            "text": res.data[i].QYMC,
            "QYSH": res.data[i].QYSH,
            "QYID": res.data[i].QYID
          };
          list.push(name);
          this.columns = list;
        }
      } else {
        this.stats = true;
      }
    });
  },
  methods: {
    // eslint-disable-next-line
    onChange(picker, value, index) {
      this.value = "当前选择公司：" + value.text;
      this.QYInfo.QYSH = value.QYSH;
      this.QYInfo.QYID = value.QYID;
      // 获取票种信息
      this.getFPsort(value.QYSH);
    },
    getFPsort(shid) {
      this.sort = [];
      let yhid = getUserInfo().YHID;
      getFptype(yhid, shid).then(res => {
        console.log(res);
        this.sort = res.data;
      });
    },
    offList() {
      this.ShowAddress = false;
    },
    getCheck(idx, FPZLDM) {
      this.checked = idx;
      this.FPZLDM = FPZLDM;
    },
    offAdd() {
      this.addAddress = false;
    },
    onAdd() {
      this.addAddress = true;
    },
    // 获取地址列表
    getaddressList(){
      let id = getUserInfo().YHID;
      getAddress(id).then(res => {
        if (res.data) {
          let listAddress = res.data;
          let arr = [];
          for(let i = 0; i < listAddress.length; i++) {
            let obj = {
              "id": i,
              "name": listAddress[i].LXR,
              "address": listAddress[i].ADDRESS,
              "tel": listAddress[i].LXR
            }
            arr.push(obj);
          }
          this.list = arr;
          console.log(arr);
        }
      })
    },
    // 选中地址
    chooseAddress(item) {
      this.userInfo.name = item.name;
      this.userInfo.tel = item.tel;
      this.userInfo.address = item.address;
    },
    // 保存地址
    onSave(val) {
      let dz = val.province + val.city + val.county + val.addressDetail;
      this.nowAddress = dz;
      let id = getUserInfo().YHID;
      saveAddress(id, val.name, val.tel, dz, this.getLYid).then(res => {
        this.$toast(res.msg);
        this.addAddress = false;
      })
    },
    pushCon() {
      // 提交发票发售信息
      if (this.QYInfo.QYSH === "" || this.fenshu === "") {
        this.$toast("有遗漏所选信息");
      } else {
        let id = getUserInfo().YHID;
        let name = getUserInfo().NAME;
        let tel = getUserInfo().PHONENO;
        faPiaoPush(id, this.QYInfo.QYSH, this.QYInfo.QYID, this.FPZLDM, `${this.fenshu}`,
          `${this.lingType}`,
          `${this.nowAddress}`,
          name,
          tel).then(res => {
            this.$toast(res.msg);
            if (res.data.STATUS === "success") {
              if (this.lingType === "1") {
                this.addAddress = true;
              }
              this.getLYid.push(res.data.LYID);
              this.$router.push("/home");
            }
          })
        }
      }
    }
  };
</script>
<style lang="stylus" scoped>
.connect
  width 100%
  height 100%
  padding-top 1.5rem
  background #ffffff
  .fpType {
    width 100%
    height 100%
    padding 0 .4rem
    .fpType-item {
      width 100%
      height auto
      position relative
      padding-top .2rem
      padding-bottom .2rem
      border-bottom 1px solid #dfdfdf
      .check-btn {
        position absolute
        color #409eff
        right .2rem
        top .8rem
        span {
          font-size 50px
        }
      }
      p {
        line-height 20px
      }
    }
  }
  .login-info {
    width: 100%;
    margin-top .2rem
    text-align: right;
    padding: 0.1rem 0.2rem;
    background: #ffffff;
  }
  .connect-user
    width 100%
    height 1.5rem
    text-align center
    line-height 1.5rem
    background #409eff
    font-size 16px
    font-weight 520
    color #ffffff
  .login-info
    width 100%
    height 100%
    text-align right
    padding .1rem .2rem
    background #ffffff
</style>
