<template>
  <div class="connect" style="padding-top:1.5rem">
    <bannerTop :headtitle="NavText"></bannerTop>
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      @add="onAdd"
      @select="chooseAddress"
      style="z-index:1000"
    />
    <!--新增地址-->
    <van-popup
      v-model="addAddress"
      position="right"
      :overlay="false"
      style="width:100%;height:100%;overflow:hidden;z-index:20000"
    >
      <van-nav-bar
        title="新增地址"
        left-text="返回"
        left-arrow
        @click-left="offAdd"
      />
      <van-address-edit
        :area-list="areaList"
        @save="onSave"
        @change-detail="onChangeDetail"
      />
    </van-popup>
  </div>
</template>
<script>
/* eslint-disable */
import bannerTop from "@/components/publichead";
import areaList from "../../../public/web/area/area";
import { getAddress, saveAddress } from "@/api/user";
import { getUserInfo } from "@/utils/userInfo";
export default {
  name: "Address",
  components: {
    bannerTop
  },
  data() {
    return {
      NavText: "地址列表",
      chosenAddressId: "1",
      addAddress: false,
      areaList: areaList,
      list: [],
      userInfo: {
        name: "",
        tel: "",
        address: ""
      }
    };
  },
  mounted: function() {
    // 获取地址列表
    this.getAddressInfo();
  },
  methods: {
    offList() {
      this.ShowAddress = false;
    },
    getCheck(idx) {
      this.checked = idx;
    },
    offAdd() {
      this.addAddress = false;
      this.getAddressInfo();
    },
    getAddressInfo() {
      let YHID = getUserInfo().YHID;
      getAddress(YHID).then(res => {
        if (res.data) {
          let listAddress = res.data;
          let arr = [];
          for (let i = 0; i < listAddress.length; i++) {
            let obj = {
              "id": i,
              "name": listAddress[i].LXR,
              "address": listAddress[i].ADDRESS,
              "tel": listAddress[i].LXR
            };
            arr.push(obj);
          };
          this.list = arr;
          console.log(arr);
        }
      });
    },
    onAdd() {
      this.addAddress = true;
    },
    // 选中地址
    chooseAddress(item) {
      this.userInfo.name = item.name;
      this.userInfo.tel = item.tel;
      this.userInfo.address = item.address;
    },
    // 保存地址
    onSave(val) {
      let YHID = getUserInfo().YHID;
      let dz = val.province + val.city + val.county + val.addressDetail;
      this.nowAddress = dz;
      saveAddress(YHID, val.name, val.tel, dz).then(res => {
        this.$toast(res.msg);
      })
    }
  }
};
</script>
<style>
.van-address-list__add {
  z-index: 100;
}
</style>
