import { server } from "../utils/http.js";
// 注册用户
export function addUser(JYM, OPENID, PASSWORD, PASSWORD1, PHONENO, YWM) {
  return server.post("/zhuce/yanzheng.action", {
    JYM,
    OPENID,
    PASSWORD,
    PASSWORD1,
    PHONENO,
    YWM
  });
}

