<template>
  <div class="connect">
    <bannerTop :headtitle="NavText"></bannerTop>
    <van-cell-group @click="show = !show">
      <van-field v-model="value" placeholder="选择绑定企业" disabled />
    </van-cell-group>
    <van-popup v-model="show" position="bottom">
      <van-picker :columns="bdqy" @change="onChange" />
    </van-popup>
    <van-cell-group>
      <van-cell title="购方信息" style="font-size:16px" />
      <van-field v-model="SHUIHAO" autofocus label="税号" />
      <van-field v-model="NAME" label="名称" />
      <van-field v-model="PHONE" label="手机号" />
      <van-field v-model="YINHANG" label="开户行" />
      <van-field v-model="YHH" label="银行号" />
      <!-- <van-cell title="征收品目" :value="XZZSPM" is-link @click="ZSPM" /> -->
      <div class="login-info">
        <van-button
          size="large"
          type="info"
          @click="checkForm"
          style="height: 38px;line-height:38px"
        >
          下一步
        </van-button>
      </div>
      <!--选择征收品目-->
      <van-popup v-model="showZSPM" position="bottom">
        <van-picker :columns="zspmCon" @change="choosePin" />
      </van-popup>
      <van-popup
        v-model="showDetil"
        position="right"
        :overlay="false"
        style="width:100%;height:100%;"
      >
        <van-nav-bar
          title="专用发票代开申请"
          left-text="返回"
          left-arrow
          @click-left="back"
        />
        <van-collapse v-model="activeName" accordion>
          <van-collapse-item title="货物明细" name="1">
            <form ref="oneData">
              <van-field required v-model="HWMC" label="货物或应税劳务名称" />
              <van-field required v-model="HWDJ" type="number" label="单价" />
              <van-field
                required
                v-model="HWSL"
                type="number"
                label="数量"
                @blur="getAllMoney"
              />
              <van-field v-model="GLDW" type="text" label="计量单位" />
              <van-field v-model="GGXH" type="text" label="规格型号" />
              <van-field disabled v-model="HWZSL" label="征收率" />
              <van-field disabled v-model="HWZJE" label="金额" />
              <van-field disabled v-model="HWZSE" label="税额" />
              <van-field disabled v-model="SJHJ" label="税价小计" />
              <div class="login-info">
                <van-button
                  size="large"
                  type="default"
                  @click="adHWList"
                  style="height: 38px;line-height:38px"
                >
                  下一条
                </van-button>
              </div>
              <div class="login-info">
                <van-button
                  size="large"
                  type="info"
                  @click="getAll"
                  style="height: 38px;line-height:38px"
                >
                  完成
                </van-button>
              </div>
            </form>
          </van-collapse-item>
        </van-collapse>
        <van-cell title="应纳税款明细" style="font-size:14px" />
        <div class="shuifei">
          <table border="1">
            <thead>
              <tr>
                <th>征收项目</th>
                <th>计费依据</th>
                <th>应纳税额</th>
                <th>普惠减证比例</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, idx) in SHUIZHONG"
                :key="idx + 'A'"
                v-if="idx <= 1"
              >
                <td>{{ item.ZSPMMC }}</td>
                <td>{{ idx === 0 ? HWZJE.toFixed(2) : YNSE.toFixed(2) }}</td>
                <td ref="allmoney">
                  {{
                    idx === 0
                      ? YNSE.toFixed(2)
                      : (YNSE * HWZSL * (1 - item.PTMZBL)).toFixed(2)
                  }}
                  <input type="hidden" ref="zhxmdm" :value="item.ZSXMDM" />
                  <input type="hidden" ref="zhpmdm" :value="item.ZSPMDM" />
                </td>
                <td>{{ item.PTMZBL }}</td>
              </tr>
              <tr
                v-for="(item, idx) in SHUIZHONG"
                :key="idx + 'A'"
                v-if="idx > 1 && HWZJE > 300000"
              >
                <td>{{ item.ZSPMMC }}</td>
                <td>{{ YNSE.toFixed(2) }}</td>
                <td ref="allmoney">
                  {{ (YNSE * HWZSL * (1 - item.PTMZBL)).toFixed(2) }}
                  <input type="hidden" ref="zhxmdm" :value="item.ZSXMDM" />
                  <input type="hidden" ref="zhpmdm" :value="item.ZSPMDM" />
                </td>
                <td>{{ item.PTMZBL }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <van-cell title="发票领取方式" style="font-size:14px" />
        <van-radio-group v-model="lingType">
          <van-cell-group>
            <van-cell title="邮寄" clickable @click="lingType = '1'">
              <span>{{ userInfo.address }}</span>
              <van-radio name="1" />
            </van-cell>
            <van-cell title="自助机" clickable @click="lingType = '2'">
              <van-radio name="2" />
            </van-cell>
          </van-cell-group>
        </van-radio-group>
        <div class="login-info">
          <van-button
            size="large"
            type="info"
            @click="pushCon"
            style="height: 38px;line-height:38px"
          >
            提交
          </van-button>
        </div>
      </van-popup>
    </van-cell-group>
    <!--新增地址-->
    <van-popup
      v-model="addAddress"
      position="right"
      :overlay="false"
      style="width:100%;height:100%;overflow:hidden;"
    >
      <van-nav-bar
        title="新增地址"
        left-text="返回"
        left-arrow
        @click-left="offAdd"
      />
      <van-address-edit :area-list="areaList" @save="onSave" />
    </van-popup>
  </div>
</template>
<script>
/* eslint-disable */ 
import bannerTop from "@/components/publichead";
import {
  queryzpdk, 
  queryszhd,
  queryQyxxlist,
  getAddress,
  saveAddress,
  faPiaoPush,
  getFptype,
  getBuyUser
} from "@/api/user";
import areaList from "../../../public/web/area/area";
import { getUserInfo } from "@/utils/userInfo";
export default {
  name: "ZhuanPiao",
  components: {
    bannerTop
  },
  data() {
    return {
      NavText: "专票代开",
      checked: false,
      value: "",
      show: false,
      showDetil: false,
      bdqy: [],
      SHUIZHONG: [],
      zspmCon: [],
      HWZSE: 0,
      getLYid: "",
      activeName: "",
      showZSPM: false,
      XZZSPM: "",
      SHUIHAO: "",
      NAME: "",
      PHONE: "",
      YINHANG: "",
      YHH: "",
      HWMC: "",
      HWDJ: 0,
      HWSL: 0,
      HWZSL: 0,
      GLDW: "",
      GGXH: "",
      lingType: "2",
      ShowAddress: false,
      chosenAddressId: "1",
      addAddress: false,
      list: [],
      nowAddress: "",
      userInfo: {
        name: "",
        tel: "",
        address: ""
      },
      areaList: areaList,
      QYInfo: {
        QYSH: "",
        QYID: ""
      },
      nowHWItem: []
    };
  },
  computed: {
    // 计算总金额
    HWZJE: function() {
      return this.HWDJ * this.HWSL;
    },
    // 增值税应纳税额
    YNSE: function() {
      return this.HWZJE * this.HWZSL;
    },
    // 税价合计
    SJHJ: {
      get: function() {
        return this.HWZJE + this.HWZSE;
      },
      set: function() {
        console.log(newValue);
      }
    } 
  },
  created: function() {
    // 获取税种信息
    this.ZSPM();
    let list = [];
    // 获取企业列表
    let id = getUserInfo().YHID;
    queryQyxxlist(id).then(res => {
      this.value = res.data[0].QYMC;
      this.QYInfo.QYSH = res.data[0].QYSH;
      this.QYInfo.QYID = res.data[0].QYID;
      // 设置默认购方信息
      this.getUser(this.value, this.QYInfo.QYSH);
      if (res.data) {
        this.stats = false;
        for (let i = 0; i < res.data.length; i++) {
          let name = {
            text: res.data[i].QYMC,
            QYSH: res.data[i].QYSH,
            QYID: res.data[i].QYID
          };
          list.push(name);
          this.bdqy = list;
        }
      } else {
        this.stats = true;
      }
    });
  },
  methods: {
    offList() {
      this.ShowAddress = false;
    },
    getCheck(idx) {
      this.checked = idx;
    },
    offAdd() {
      this.addAddress = false;
    },
    onAdd() {
      this.addAddress = true;
    },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    back() {
      this.showDetil = false;
    },
    checkForm() {
      this.showDetil = true;
    },
    // 选中征收品目/征收率
    choosePin(idx, item) {
      this.XZZSPM = item.text;
      this.HWZSL = item.ZSL;
      console.log(item);
    },
    // eslint-disable-next-line
    // 选择当前公司
    onChange(picker, value, index) {
      this.value = "当前选择公司：" + value.text;
      this.QYInfo.QYSH = value.QYSH;
      this.QYInfo.QYID = value.QYID;
      // 获取购方信息
      this.getUser(value.text, value.QYSH);
    },
    // 获取购方信息
    getUser(value, QYSH) {
      getBuyUser(value, QYSH).then(res => {
        if (!res.data) {
          this.$toast("暂无企业详细信息,请手段输入");
        } else {
          this.SHUIHAO = res.data.GFSH;
          this.NAME = res.data.GFMC;
          this.PHONE = res.data.PHONENO;
          this.YINHANG = res.data.BANKNAME;
          this.YHH = res.data.BANKNO;
        }
      })
    },
    // 显示征收品目获取税种信息
    ZSPM() {
      // this.showZSPM = !this.showZSPM;
      let id = getUserInfo().YHID;
      queryszhd(id, this.SHUIHAO).then(res => {
        let result = res.data;
        let zspm = [];
        // 设置增收率（货物明细）
        const that = this;
        if (result) {
          that.stats = false;
          for (let i = 0; i < result.SZXX.length; i++) {
            let name = {
              text: result.SZXX[i].ZSXMMC,
              ZSPMDM: result.SZXX[i].ZSPMDM,
              ZSL: result.SZXX[i].ZSL
            };
            zspm.push(name);
            that.zspmCon = zspm;
            that.SHUIZHONG = result.SZXX;
          }
        } else {
          that.stats = true;
        }
      });
    },
    getAllMoney() {
      // 获取当前列数据
      // let all = this.$refs.allmoney;
      // let money = 0;
      // for (let i = 0; i < all.length; i++) {
      //   money += Number(all[i].innerText);
      // }
      // this.HWZSE = money;
    },
    pushCon() {
      // 货物明细
      let HWMXDETAIL = [
        {
          SL: this.HWSL,
          GGXH: this.GGXH,
          JSHJ: this.SJHJ,
          JLDW: this.JSHJ,
          HWMC: this.HWMC,
          DJ: this.HWDJ
        }
      ];
      // 纳税明细
      let nsje = this.$refs.allmoney;
      let YNSKMXDETAIL = [];
      if (nsje) {
        for (let i = 0; i < nsje.length; i++) {
          let nsobj = {
            ZSPMDM: "",
            ZSXMDM: "",
            YNSE: "",
            JSFYJ: ""
          }
          nsobj.JSFYJ = this.HWZJE;
          nsobj.YNSE = Number(nsje[i].innerText);
          nsobj.ZSXMDM = nsje[i].children[0].value;
          nsobj.ZSPMDM = nsje[i].children[1].value;
          YNSKMXDETAIL.push(nsobj);
        }
      } else {
        this.$toast("暂无缴纳明细信息");
      }
      // 提交请求
      let id = getUserInfo().YHID;
      queryzpdk(
        id, this.QYInfo.QYSH, this.SHUIHAO, 
        this.NAME, this.userInfo.address, this.userInfo.tel, 
        this.YINHANG, this.YHH, this.HWZSE, this.HWZJE,
        this.SJHJ, this.lingType, HWMXDETAIL, YNSKMXDETAIL
        ).then(res => {
          this.$toast(res.msg);
          if (res.data.STATUS === "success") {
            this.addAddress = true;
            this.getLYid = res.data.LYID;
          }
      })
    },
    // 保存地址
    onSave(val) {
      let dz = val.province + val.city + val.county + val.addressDetail;
      this.nowAddress = dz;
      let id = getUserInfo().YHID;
      saveAddress(id, val.name, val.tel, dz, this.getLYid).then(res => {
        this.$toast(res.msg);
        this.addAddress = false;
      })
    },
    // 下一条
    adHWList() {
      if (this.nowHWItem.length > 4) {
        this.$toast("最多添加五条")
        return
      } else {
        let addhwObj = {
            SL: this.HWSL,
            GGXH: this.GGXH,
            JSHJ: this.SJHJ,
            JLDW: this.JSHJ,
            HWMC: this.HWMC,
            DJ: this.HWDJ
          }
        this.nowHWItem.push(addhwObj);
        this.HWSL = "";
        this.GGXH = "";
        this.SJHJ = "";
        this.JSHJ = "";
        this.HWMC = "";
        this.HWDJ = "";
        console.log(this.nowHWItem);
      }
      return false;
    },
    // 保存所有信息
    getAll() {

    }
  }
};
</script>
<style lang="stylus" scoped>
.connect
  width 100%
  height 100%
  padding-top 1.5rem
  background #ffffff
  .shuifei
    width 100%
    height auto
    padding .2rem
    table
      width 100%
      border 1px solid #dfdfdf
      text-align center
      tr
        line-height .8rem
  .login-info
    width 100%
    height 1rem
    text-align right
    padding .1rem .2rem
    background #ffffff
  .check-true
    width 100%
    height 1rem
    line-height 1rem
    padding 0 10px
    display flex
    justify-content space-between
</style>
