<template>
  <div class="home">
    <div class="banner-top">
      <div class="banner-text">
        <div class="left-con">
          <div class="headimg">
            <img :src="userInfo.img" />
          </div>
          <span>{{ userInfo.name }}</span>
        </div>
        <van-icon
          @click="show = !show"
          name="wap-nav"
          color="#ffffff"
          size="24px"
        />
      </div>
      <div class="banner-main">
        <div class="banner-item" v-for="(item, idx) in bannerdata" :key="idx">
          <router-link :to="item.page" class="banner-item">
            <div class="banner-icon">
              <img :src="item.img" />
            </div>
            <span>{{ item.text }}</span>
          </router-link>
        </div>
      </div>
    </div>
    <Nav :alldata="alldata"></Nav>
    <van-popup v-model="show" position="right" style="width:30%;height:100%">
      <van-cell-group>
        <van-cell title="首页" is-link to="home" />
        <van-cell title="个人中心" is-link to="person" />
      </van-cell-group>
    </van-popup>
  </div>
</template>
<script>
import Nav from "@/components/nav.vue";
import alldata from "@/utils/navdata";
import { getUserInfo, setUserInfo } from "@/utils/userInfo";
import { loginOpenid } from "@/api/user";
export default {
  name: "home",
  components: {
    Nav
  },
  data() {
    return {
      alldata: alldata.home,
      title: "最新动态",
      show: false,
      userInfo: {
        img: "",
        name: ""
      },
      bannerdata: [
        {
          id: 0,
          text: "企业绑定",
          img: require("@/assets/img/qirenzheng.png"),
          page: "connect"
        },
        {
          id: 1,
          text: "实名认证",
          img: require("@/assets/img/fuwu.png"),
          page: "renzheng"
        },
        {
          id: 2,
          text: "历史记录",
          img: require("@/assets/img/faguan.png"),
          page: "historyPage"
        }
      ]
    };
  },
  mounted: function() {
    let Info = getUserInfo();
    this.userInfo.img = Info.headimgurl;
    this.userInfo.name = Info.nickname;
    loginOpenid(Info.openid).then(openidUser => {
      var users = openidUser.data;
      // 用户未注册,跳转注册页面进行注册
      if (!users || users.STATUS === "UNREGISTER") {
        this.$router.push({ path: "/sigin" });
        return;
      } else {
        setUserInfo(
          Info.headimgurl,
          Info.nickname,
          Info.openid,
          users.YHID,
          users.NAME,
          users.PHONENO,
          users.SFZH,
          users.SMRZZT
        );
      }
      if (users.SMRZZT === "PASS") {
        return;
      } else {
        this.$dialog
          .confirm({
            message: "未实名认证,部分功能无法使用"
          })
          .then(() => {
            this.$router.push({ path: "/renzheng" });
          });
      }
    });
  }
};
</script>
